import React from "react";

import { IApiCharacters } from "../../../intefaces";

import Card from "./Card";

const CardsContainer: React.FC<{ results: IApiCharacters[] }> = props => {
  const { results } = props;
  return (
    <div className="cards">
      {results.map(result => {
        const { _id: id } = result;

        return <Card data={result} key={id} />;
      })}
    </div>
  );
};

export default CardsContainer;
