import React from "react";
import { NavLink } from "react-router-dom";

import { navLinks } from "../../../store";

const Nav = (): JSX.Element => {
  return (
    <nav className="nav">
      <ul className="nav__list">
        {navLinks.map(navLink => {
          return (
            <li className="nav__list__item" key={`${navLink.url}li`}>
              <NavLink
                activeClassName="link-active"
                className="nav__list__item__link"
                key={navLink.url}
                exact
                to={navLink.url}
              >
                {navLink.label}
              </NavLink>
            </li>
          );
        })}
      </ul>
    </nav>
  );
};

export default Nav;
