import React, { useRef, useState } from "react";

import { IFetchStatus, IRequest, ISearchBarData } from "../../../intefaces";
import { defaultRequest } from "../../../store";

import useFetch from "../../../Logic/useFetch";
import createlUrlFetchAll from "../../../Logic/createUrlFetchAll";
import SearchBarView from "./SearchBarView";

const SearchBar = (): JSX.Element => {
  const [request, setRequest] = useState<IRequest>({ ...defaultRequest });

  const [searchText, setSearchText] = useState<string>("");

  const debugMode = useRef(false);

  const url = createlUrlFetchAll(request);

  const fetchStatus: IFetchStatus = useFetch({ request, debugMode, url });

  const { isLoading } = fetchStatus;

  const checkRequest = () => {
    if (isLoading) {
      return;
    }

    if (searchText !== request.content) {
      debugMode.current = true;

      setRequest(prev => ({ ...prev, page: "1" }));

      debugMode.current = false;
    }

    setRequest(prev => ({ ...prev, content: searchText }));
  };

  const changeRequest = (text: string, type: string) => {
    debugMode.current = true;

    checkRequest();

    debugMode.current = false;

    setRequest(prev => ({ ...prev, [type]: text }));
  };

  const setSearchLine = (value: string) => {
    setSearchText(value);
  };

  const searchBarData: ISearchBarData = {
    changeRequest,
    setSearchLine,
    checkRequest,
    fetchStatus,
    request,
    searchText,
  };

  return <SearchBarView searchBarData={searchBarData} />;
};

export default SearchBar;
